import Header from "./Components/Header";
import "./App.css";
import Title from "./Components/Title";
import CategoryButton from "./Components/CategoryButton";
import { Alert, Container } from "react-bootstrap";
import MealCard from "./Components/MealCard";
import { categories, meals } from "./Data/data";
import { useEffect, useState } from "react";

function App() {
  const [mealsArray, setMealsArray] = useState(meals);
  const [currentCategory, setCurrentCategory] = useState("");

  let newMealsArray = [];

  const changeCategory = (e) => {
    setCurrentCategory(e.innerHTML);
  };

  const searchHandler = (e) => {
    if (e === "") {
      newMealsArray = meals;
    } else {
      newMealsArray = meals.filter((meal) => meal.name.includes(e));
    }
    setMealsArray(newMealsArray);
  };

  useEffect(() => {
    if (currentCategory === "") {
      newMealsArray = meals;
    } else {
      newMealsArray = meals.filter((meal) => meal.category === currentCategory);
    }
    setMealsArray(newMealsArray);
  }, [currentCategory]);

  return (
    <div className="font">
      <Header searchHandler={(e) => searchHandler(e)} />
      <Title text="قائمة الطعام" />
      <Container className="navigation">
        <CategoryButton text="الكل" onClick={() => setCurrentCategory("")} />
        {categories.map((category) => {
          return (
            <CategoryButton
              text={category}
              onClick={(e) => changeCategory(e.target)}
            />
          );
        })}
      </Container>
      <Container className="meals">
        {mealsArray.length === 0 ? (
          <Alert variant="danger">لا يوجد وجبات تطابق البحث</Alert>
        ) : (
          mealsArray.map((meal) => {
            return (
              <MealCard
                mealTitle={meal.name}
                mealPrice={meal.price}
                mealDesc={meal.desc}
                mealImg="imgs/test.jpg"
              />
            );
          })
        )}
      </Container>
    </div>
  );
}

export default App;
