import { Col, Container, Row } from "react-bootstrap";
import "./MealCard.css";
import React from "react";

const MealCard = ({ mealTitle, mealPrice, mealDesc, mealImg }) => {
  return (
    <Container className="meal">
      <Row className="meal-card">
        <Col className="meal-img" sm="12" md="2">
          <img
            src={require(`../../${mealImg}`)}
            alt="meal"
            className="img-fluid"
          />
        </Col>
        <Col className="meal-info" sm="12" md="10">
          <Row className="meal-title-price">
            <div className="meal-title">{mealTitle}</div>
            <div className="meal-price">{mealPrice} جنيه</div>
          </Row>
          <Row className="meal-desc">{mealDesc}</Row>
        </Col>
      </Row>
    </Container>
  );
};

export default MealCard;
