import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import "./Title.css";

const Title = ({ text }) => {
  return (
    <Container className="title-cont">
      <Row>
        <Col className="w-100 title-col">
          <div className="title">{text}</div>
        </Col>
      </Row>
    </Container>
  );
};

export default Title;
